// import processDetail from '@/sub_user/pages/progress/components/progress'
export default{
	components:{
		// processDetail
	},
	data(){
		return {
			current:null,
			bool:false
		}
	},
	methods:{
		//折叠面板
		showClick(index){
			// 1)都未展开的
			// 2)都展开的
			// 3)有一个展开的了，但你点击的这个都没展开
			if(this.bool === false && this.current !== index){
				this.bool = true
				this.current = index
			}else if(this.bool === true && this.current === index){
				this.bool = false
				this.current = null
			}else if(this.bool === true && this.current !== index){
				this.bool = true
				this.current = index
			}
		},
		invitedProcess(){
			uni.navigateTo({
				url:'/sub_user/pages/progress/index'
			})
		}
	},
	onPullDownRefresh() {
		console.log('刷新了一次');
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onReachBottom(){
		console.log('到底了')
	}
}
